import React from 'react';
import GoogleMapReact from 'google-map-react';
import { Typography } from '@material-ui/core';
import LocationOn from '@material-ui/icons/LocationOn';

import mapStyles from '../../mapStyles';
import useStyles from './styles.js';


const Map = ({ coords, places, setCoords, setBounds, setChildClicked, cardClicked }) => {
  // const bigScreen = useMediaQuery('(min-width:600px)');
  const classes = useStyles();

  return (
    <div className={classes.mapContainer}>
      <GoogleMapReact
        bootstrapURLKeys={{ key: process.env.REACT_APP_GOOGLE_MAP_API_KEY }}
        defaultCenter={coords}
        center={coords}
        defaultZoom={14}
        margin={[50, 50, 50, 50]}
        options={{ disableDefaultUI: false, zoomControl: true, styles: mapStyles }}
        onChange={(e) => {
          setCoords({ lat: e.center.lat, lng: e.center.lng }); // notice that this changes where you would get directions from
          setBounds({ ne: e.marginBounds.ne, sw: e.marginBounds.sw });
        }}
        onChildClick={(child) => setChildClicked(child)}
      >
        {places.map((place, i) => (
          <div
            className={classes.markerContainer}
            lat={Number(place.lat)}
            lng={Number(place.lng)}
            key={i}
          >
              <LocationOn color="secondary" fontSize="large" className={classes.pointer}/>
              <Typography className={classes.placeName} variant="subtitle2" gutterBottom>{place.place_name}</Typography>
          </div>
        ))}
      </GoogleMapReact>
    </div>
  );
};

export default Map;
