import React from 'react';
import DropDown from './Dropdown';


const AppBar = (props) => {
  return (
    <div class="appbar">
      <div class="searchbar">
        <div class="text">
          Search Place or Deal...
        </div>
      </div>
      <div class="filterbar">
        <DropDown
          options={props.sortByOptions}
          selection={props.sortBy}
          setSelection={props.setSortBy}
          textPrompt={'Sort By'}
        />
        <DropDown
          options={props.timeFilterOptions}
          selection={props.timeFilter}
          setSelection={props.setTimeFilter}
        />
        <DropDown
          options={props.dealsFilterOptions}
          selection={props.dealsFilter}
          setSelection={props.setDealsFilter}
        />
      </div>
    </div>
  );
}

export default AppBar;
