import React from 'react';
import { Box, Typography, Button, Card, CardMedia, CardContent, CardActions, ButtonGroup } from '@material-ui/core';
import Rating from '@material-ui/lab/Rating';
import Collapse from '@material-ui/core/Collapse';
// eslint-disable-next-line
import clsx from 'clsx';
import IconButton from '@material-ui/core/IconButton';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import useStyles from './styles.js';
import { getTodaysTime, formatTimeExpand } from '../../utils';


const placeHolderPhoto = 'https://www.foodserviceandhospitality.com/wp-content/uploads/2016/09/Restaurant-Placeholder-001.jpg';

const mkPhotoUrl = (place) => {
  const photoRef = place.photos && place.photos[0];
  if (!photoRef) {
    return placeHolderPhoto;
  }
  return `https://maps.googleapis.com/maps/api/place/photo?photo_reference=${photoRef}&key=AIzaSyCxfXAPgVM8ownCZdr8dFbQSg76chB7P4s&maxheight=350`;
};

export const formatTodaysTime = (time, timeFilter) => 
  timeFilter === 'now' ? 'Until ' + formatTimeExpand(time.end_time) : formatTimeExpand(time.start_time) + ' - ' + formatTimeExpand(time.end_time);

export const formatDiscount = (amount, marker) => marker === '$' ? marker + amount : amount + marker;

export const elipseFormat = (maxLength, text) => text.length > maxLength ? text.substring(0, maxLength) + '...' : text;

const PlaceDetails = ({ place, selected, refProp, timeFilter, setCoords }) => {
  if (selected) refProp?.current?.scrollIntoView({ behavior: 'smooth', block: 'start' });
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  // onMouseEnter={() => setCoords({lat: place.lat, lng: place.lng})}
  return (
    <Card elevation={6} className={classes.root} > 
      <div className={classes.details}>
        <CardContent className={classes.content}>
          <Typography gutterBottom variant="h5">{place.place_name}</Typography>
          <Box display="flex" my={0}>
            <Box display="flex" mr={1}>
              <Rating value={Number(place.rating)} readOnly />
            </Box>
            <Typography>({place.user_ratings_total})</Typography>
          </Box>
          <Box display="flex" justifyContent="space-between" my={1} alignItems="center">
            <Typography variant="subtitle2" color="textSecondary">{formatTodaysTime(getTodaysTime(place.times), timeFilter)}</Typography>
          </Box>
          <hr></hr>
          {place.deals.slice(0, 5).map((deal) => (
            <Box display="flex" justifyContent="space-between" alignItems="center">
              <Typography variant="subtitle2" color="textSecondary">{formatDiscount(deal.discount_amount, deal.discount_marker)}: {elipseFormat(35, deal.item)}</Typography>
            </Box>
          ))}
          {place.deals.length > 5
            && (
              <>
                <CardActions disableSpacing>
                  <Typography variant="subtitle2" color="textSecondary">+ {place.deals.length - 5} more!</Typography>
                  <IconButton
                    className={clsx(classes.expand, {
                      [classes.expandOpen]: expanded,
                    })}
                    onClick={handleExpandClick}
                    aria-expanded={expanded}
                    aria-label="show more"
                  >
                    <ExpandMoreIcon />
                  </IconButton>
                </CardActions>
                <Collapse in={expanded} timeout="auto" unmountOnExit>
                  {place.deals.slice(5).map((deal) => (
                    <Box display="flex" justifyContent="space-between" my={1} alignItems="center">
                      <Typography variant="subtitle2" color="textSecondary">{formatDiscount(deal.discount_amount, deal.discount_marker)}: {elipseFormat(35, deal.item)}</Typography>
                    </Box>
                  ))}
                </Collapse>
              </>
            )}

        </CardContent>
        <CardActions>
          <ButtonGroup variant='outlined'>
            {place.formatted_address && (
              <Button size="small" color="seconday" onClick={() => window.open(place.gmap_url, '_blank')}>
                Google Maps
              </Button>
            )}
          </ButtonGroup>
        </CardActions>
      </div>
      <CardMedia
        className={classes.image}
        image={mkPhotoUrl(place)}
      />
    </Card>
  );
};

export default PlaceDetails;
