/* eslint-disable consistent-return */
import axios from 'axios';
import ReactGA from 'react-ga';
import { day, time, formatTimeCondense } from '../utils';


const handleTimeFilter = (timeFilter) =>
  timeFilter.toLowerCase() === 'now' ? `now/${day}%20${time}` : `now/${day}%20${formatTimeCondense(timeFilter)}`;


export const getPlacesData = async (timeFilter) => {
  try {
    const { data } = await axios.get(`https://gbjt9fglc8.execute-api.us-west-2.amazonaws.com/api/places/${handleTimeFilter(timeFilter)}`);
    return data;
  } catch (error) {
    console.log(error);
    ReactGA.exception({
      description: 'Error fetching data',
      fatal: true
    });
    return [];
  }
};
