import React, { useState, useEffect } from 'react';
import { Grid } from '@material-ui/core';
import { MuiThemeProvider, createTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';

import { getPlacesData } from './api/api';
import Header from './components/Header/Header';
import List from './components/List/List';
import Map from './components/Map/Map';

import ReactGA from 'react-ga';
import Mobile from './components/Mobile/Mobile';
const TRACKING_ID = "G-0JVNW6YKDD";
ReactGA.initialize(TRACKING_ID);


// use default theme
// const theme = createMuiTheme();

// Or Create your Own theme:
const theme = createTheme({
  palette: {
    primary: {
      main: '#EFC319',
    },
    secondary: {
      main: '#bb94e3',
    },
  },
});

const sortByOptions = ['Number of Deals', 'Popularity']; // 'Finish Time', 'Distance', 
const timeFilterOptions = ['Now', '4:00 PM', '4:30 PM', '5:00 PM', '5:30 PM', '6:00 PM', '6:30 PM'];
const dealsFilterOptions = ['All Deals', 'Margaritas', 'Beer', 'Wine', 'Cocktails', 'Tacos'];


const getCompareFcn = (sortBy) => 
  (place1, place2) => {
    const getSortValue = (place, sortBy) => {
      var value;
      if (sortBy === 'Popularity') {
        value = 0;
      }
      sortBy = sortByOptions[sortBy];
      if (sortBy === 'Number of Deals') {
        value = place.deals.length;
      }
      if (sortBy === 'Popularity') {
        value = place.user_ratings_total;
      }
      return value;
    }
    return getSortValue(place1, sortBy) - getSortValue(place2, sortBy);
  }


const makeUserFilter = (dealsFilter) => 
  (place) => {
    let filter = dealsFilterOptions[dealsFilter].toLowerCase();
    if (filter === 'all deals') {
      return true;
    }
    if (filter.endsWith('s')) {
      filter = filter.substr(0, filter.length-1)
    }
    const dealWords = place.deals.map(deal => deal.item).join(' ').replace(',', '').toLowerCase().split(' ')
    return dealWords.includes(dealsFilterOptions[dealsFilter].toLowerCase())
  }

const App = () => {
  const [coords, setCoords] = useState({});
  // eslint-disable-next-line
  const [bounds, setBounds] = useState(null);

  const [filteredPlaces, setFilteredPlaces] = useState([]);
  const [places, setPlaces] = useState([]);

  const [childClicked, setChildClicked] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  const [sortBy, setSortBy] = React.useState('');
  const [timeFilter, setTimeFilter] = React.useState(0);
  const [dealsFilter, setDealsFilter] = React.useState(0);

  useEffect(() => {
    navigator.geolocation.getCurrentPosition(({ coords: { latitude, longitude } }) => {
      setCoords({ lat: latitude, lng: longitude });
    });
  }, []);

  useEffect(() => {
    const compareFcn = getCompareFcn(sortBy);
    const sorted = places.sort(compareFcn).reverse();
    const userFilter = makeUserFilter(dealsFilter);
    setFilteredPlaces(sorted.filter(userFilter));
  }, [dealsFilter, sortBy, places]);

  useEffect(() => {
    setIsLoading(true);

    getPlacesData(timeFilterOptions[timeFilter])
      .then(places => {
        places = places.filter((place) => place.deals.length > 0);
        setPlaces(places);
        setIsLoading(false);
      });
  }, [timeFilter]);

  const bigScreen = useMediaQuery('(min-width:600px)');

  return (
    <MuiThemeProvider theme={theme}>
      {bigScreen ? (
        <>
          <Header />
          <Grid container spacing={3} style={{ width: '100%' }}>
            <Grid item xs={12} md={4}>
              <List
                isLoading={isLoading}
                childClicked={childClicked}
                setCoords={setCoords}
                places={filteredPlaces}
                timeFilter={timeFilter}
                setTimeFilter={setTimeFilter}
                coords={coords}
              />
            </Grid>
            <Grid item xs={12} md={8} style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
              <Map
                setChildClicked={setChildClicked}
                setBounds={setBounds}
                setCoords={setCoords}
                coords={coords}
                places={filteredPlaces}
              />
            </Grid>
          </Grid>
        </>
      ) : (
      <Mobile
        places={filteredPlaces}
        isLoading={isLoading}
        sortBy={sortBy}
        setSortBy={setSortBy}
        timeFilter={timeFilter}
        setTimeFilter={setTimeFilter}
        dealsFilter={dealsFilter}
        setDealsFilter={setDealsFilter}
        sortByOptions={sortByOptions}
        timeFilterOptions={timeFilterOptions}
        dealsFilterOptions={dealsFilterOptions}
      />)}
    </MuiThemeProvider>
  );
};

export default App;
