import { Select, MenuItem } from '@material-ui/core'; 
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  dropDown: {
    'fontSize': 12, backgroundColor: 'white', borderRadius: '10px 10px 0  0', width: '90px', paddingLeft: '10px'
  }
}));

const DropDown = ({ options, selection, setSelection, textPrompt }) => {
  const classes = useStyles();

  const updateSelected = (e) => setSelection(e.target.value);

  return (
    <Select
      displayEmpty
      value={selection}
      onChange={updateSelected}
      className={classes.dropDown}
    >
      {textPrompt && 
        <MenuItem value="" disabled style={{fontSize: 10}}>{textPrompt}</MenuItem>
      }
      {options.map((option, i) => (
        <MenuItem value={i} key={i} style={{fontSize: 10}}>{option}</MenuItem>
      ))}
    </Select>
  );
}

export default DropDown;
